      'Desc1 video'
      'Desc1 video'
      ;
    gap: 0;
    /* padding: 1vh 0; */
    font-family: minion, sans-serif;
  }
  
  .home .project4 .grid-container4 > div {
    background-color: rgba(255, 255, 255, 0.8);
    text-align: center;
    padding: 20px 0 0 0;
    font-size: 15px;
    font-weight: 300;
  }

