.WorkTab .title11 {
    
    padding: 10vh 0 0 0;
    font-size: 40px;
    text-align: left;
    font-family: proxima-nova-extra-condensed, sans-serif;
    
  }

  .title11 a{
    background-color:  white;
    color: black;
    letter-spacing: .5rem;
  }
  
  .description11 {
    text-align: left;
    font-size: 15px;
    font-weight: 300;
  
  }
  
  .containerForWorks .grid-container11 {
      display: inline-grid;
      grid-template-columns: 1fr 1fr 1fr;
      background-color: white;
      
      padding: 5vh 0;
      column-gap: 1vw;
      row-gap: 10px;
      
  
    }
    
    .grid-item {
     
      /* padding: 10vh 10vw 10vh 10vw; */
      font-size: 30px;
      text-align: left;
      color: white;
      border-radius: 10px;
    }

    .gi1{
      background-color: #5aa0b7;
    }

    .gi3{
      background-color: #23a8b1;
    }

    .gi2{
      background-color: #b3e3c7;
    }
  
    img {
      object-fit: cover;
      width: 100%;
      margin: 0;
      border-radius: 5px;
    }

    .grid-container11 figcaption {
        color: black;
        font-size: 15px;
        font-weight: 400;
        color: white;
    }

    .gi1 figcaption {
      color: #fcc88c;
    }

    .grid-container11 .gi1 .projName {
      color: #fcc88c;
      text-align: left;
      margin: 0;
      font-size: 40px;
      display: inline;
      font-family: proxima-nova-extra-condensed, sans-serif;
    }

    .grid-container11 .gi1 .projName .date {
      color: #fcc88c;
      text-align: right;
      font-size: 10px;
      display: inline;
    }


    .gi3 figcaption {
      color: #dbf868;
    }

    .grid-container11 .gi3 .projName {
      color: #dbf868;
      text-align: left;
      margin: 0;
      font-size: 40px;
      font-family: proxima-nova-extra-condensed, sans-serif;
    }
    .grid-container11 .gi3 .projName .date {
      color: #dbf868;
      text-align: right;
      font-size: 10px;
      display: inline;
    }

    .gi2 figcaption {
      color: #223ffc;
    }

    .grid-container11 .gi2 .projName {
      color: #223ffc;
      text-align: left;
      margin:0;
      font-size: 40px;
      font-family: proxima-nova-extra-condensed, sans-serif;
    }

    .grid-container11 .gi2 .projName .date {
      color: #223ffc;
      text-align: right;
      font-size: 10px;
      display: inline;
    }


    .grid-item img:hover{
        box-shadow: 0 3px 10px rgb(0 0 0 / 0.2);
    }
