.home .project3 .grid-container3 {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-areas:
      'genDesc genDesc'
      'second second'
      'third third'
      'forth forth'
      'fifth fifth'
      'sixth sixth'
      'seventh seventh'
      'eighth eighth'
      'video video'
      'tenth tenth'
      'eleventh eleventh'
      'twelveth twelveth';
    gap: 1vw;
    padding: 5vh 0;
    font-family: minion, sans-serif;
    text-align: justify;
  }

  .home .project3 .grid-container3 > div {
    background-color: rgba(255, 255, 255, 0.8);
    text-align: center;
    padding: 20px 0;
    text-align: justify;
    font-size: 20px;
    font-weight: 300;
  }

