}

.part1 { 
    grid-area: genDesc; 
  }
  .part2 { 
    grid-area: Desc1; 
  }
  .part3 { 
    grid-area: photo1; 
  }
  .part4 { 
    grid-area: Desc2; 
  }
  .part5 { 
    grid-area: photo2; 
  }
  .part6 {
    grid-area: Desc3;
  }
  .part7 {
    grid-area:photo3;
  }

  .home .project6 .grid-container6 {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-areas:
      'genDesc genDesc'
      'Desc1 photo1'
      'Desc1 photo1'
      'photo2 Desc2'
      'photo2 Desc2'
      'Desc3 photo3'
      'Desc3 photo3'
      ;
    gap: 1vw;
    padding: 5vh 0;
    font-family: minion, sans-serif;
  }
  
  .home .project6 .grid-container6 > div {
    background-color: rgba(255, 255, 255, 0.8);
    text-align: center;
    padding: 20px 0;
    font-size: 15px;
    font-weight: 300;
  }


